/*!
 * Version    : 2.2.0
 * Author     : Geoff Thibeau
 * Author URI : https://geoffthibeau.info
*/
*
, html
, body
, th
, td {
  margin: 0;
  padding: 0;
}

*
, *:before
, *:after {
  box-sizing: border-box;
}

:focus {
  outline: 0;
}

article
, aside
, canvas
, details
, figcaption
, figure
, footer
, header
, hgroup
, nav
, section
, summary
, video {
  display: block;
}

ins {
  text-decoration: none;
}

del {
  text-decoration: line-through;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

fieldset
, img {
  border: 0;
}

address
, caption
, cite
, code
, dfn
, em
, strong
, th
, var {
  font-style: normal;
  font-weight: normal;
}

ol
, ul
, li {
  list-style: none;
}

caption
, th {
  text-align: left;
}

h1
, h2
, h3
, h4
, h5
, h6 {
  font-size: 100%;
  font-weight: normal;
}

q:before
, q:after {
  content: '';
}

strong {
  font-weight: 700;
}

em {
  font-style: italic;
}

a img {
  border: 0;
}

* + * {
  margin-top: 1em;
}

br + br
, nav * + *
, p * + *
, table * + * {
  margin-top: auto;
}

html {
  background-color: #333;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Helvetica, Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol";
  font-size: 1rem;
  line-height: 1.7;
  background-color: #e9e9e5;
  color: #2a2000;
}

h1 {
  font-family: "Abril Fatface", Georgia, serif;
  font-size: 3rem;
  font-weight: 400;
  line-height: 1.2;
}

@media (min-width: 30em) {
  h1 {
    font-size: 4.5rem;
  }
}

h2 {
  font-size: 1.5rem;
  font-weight: 300;
  line-height: 1.2;
}

@media (min-width: 30em) {
  h2 {
    font-size: 2.25rem;
  }
}

h1 + h2 {
  margin-top: .5em;
}

.positions + h2 {
  margin-top: 2em;
}

h3 {
  font-size: .9rem;
  letter-spacing: .1em;
  text-transform: uppercase;
}

h3
, h4 {
  font-weight: 700;
}

h5
, h6 {
  font-weight: 400;
}

h4
, h5
, h6 {
  font-size: 1rem;
}

small {
  font-size: .9rem;
  line-height: inherit;
}

abbr {
  font-size: .9rem;
  letter-spacing: .07em;
  border-bottom: 1px dashed;
  cursor: help;
}

a {
  padding-right: .25em;
  padding-left: .25em;
  text-decoration: underline;
  text-decoration-color: #ff7100;
  outline: none;
  border-radius: 3px;
  background-image: linear-gradient(135deg, #ff7100, #d45600);
  background-size: 0;
  color: inherit;
}

a:focus, a:hover {
  background-size: auto;
  text-decoration-color: currentColor;
}

a img {
  border: none;
}

blockquote {
  margin: 1.5em 3em 0 1em;
  padding-left: 2em;
}

table {
  width: 100%;
}

tr.odd {
  background-color: #d1d2d0;
}

tr:hover {
  background-color: #888;
  color: #fff;
}

td {
  padding: .5em;
}

li {
  margin-top: .5em;
  margin-left: 1em;
  list-style: disc;
}

.container {
  min-height: 20em;
  margin: 0 auto;
  padding: 0 .5em 1em;
  font-size: 1rem;
  text-align: left;
}

@media (min-width: 45em) {
  .container {
    display: flex;
    justify-content: center;
    position: relative;
  }
}

@media (min-width: 45em) {
  header {
    width: 200px;
    position: sticky;
    top: 0;
    align-self: flex-start;
  }
}

.contact li {
  margin: 0;
  padding: 0;
  list-style: none;
}

.brand {
  width: 200px;
  margin: 0;
  padding: 29px 0 6px;
  display: block;
  text-decoration: none;
  border-radius: 0;
  background: none;
}

.brand img {
  width: 100%;
  height: auto;
}

main {
  max-width: 40em;
}

@media (min-width: 45em) {
  main {
    margin-top: 0;
    padding: 3em 2.66em 4em;
  }
}

.circle-text {
  width: 12em;
  height: 12em;
  margin: 1em 1em 0 0;
  padding: 2em;
  position: relative;
  display: flex;
  align-items: center;
  font-size: 100%;
  font-weight: 700;
  text-transform: uppercase;
  line-height: 1;
  text-align: center;
  border: 1px solid;
  border-color: #ff7100;
  border-color: rgba(255, 113, 0, 0.7);
  background-color: #ff7100;
  background-color: rgba(255, 113, 0, 0.7);
  background-image: linear-gradient(135deg, #ff7100, #d45600);
  border-radius: 100%;
}

.positions {
  margin-left: 0;
  display: flex;
  flex-wrap: wrap;
}

footer {
  padding: 2em .5em;
  font-size: 1rem;
  border-top: 0.3em solid #ff7100;
  background-color: #333;
  color: #888;
}

@media (min-width: 45em) {
  footer {
    text-align: center;
  }
}

@media print {
  .container
, footer {
    font-size: 10pt;
  }
  main {
    width: 6in;
    max-width: 100%;
    margin: 0;
  }
  .positions {
    width: 9in;
    max-width: none;
  }
  .position:nth-child(odd) {
    margin-left: 1em;
  }
  .position:first-child {
    margin-left: 0;
  }
  .circle-text {
    border-color: #fafafa;
    background-color: #fafafa;
    -webkit-print-color-adjust: exact;
  }
  .page-break {
    page-break-before: always;
  }
}

/*# sourceMappingURL=style.css.map */